<template>
	<scroll-view scroll-y='true' class="content" @scroll='scrollMain'>
        <!-- heads -->
        <heads :sTop='scrollTop' :title='"歌窝里音乐"'></heads>
        <view class="header">
            <!-- swiper -->
            <swiper class="index_swiper">
                <swiper-item  v-for="(item, index) in swiperList" :key="index">
                    <view class="swiper-item">
                        <image :src="item"></image>
                    </view>
                </swiper-item>
            </swiper>
            <!-- nav -->
            <view class="nav">
                <view class="navs" @click="toUrl('/pages/shopSing/shopSing')">
                    <image src="@/static/icon/nav1.png"></image>
                    <view>在售歌单</view>
                </view>
                <view class="navs">
                    <image src="@/static/icon/nav2.png"></image>
                    <view>制作人</view>
                </view>
                <view class="navs">
                    <image src="@/static/icon/nav3.png"></image>
                    <view>音乐商城</view>
                </view>
                <view class="navs">
                    <image src="@/static/icon/nav4.png"></image>
                    <view>互动社区</view>
                </view>
            </view>
        </view>
        
        <!-- 我是音乐人 -->
        <view class="isSing" v-if="haveAcquisitionActivity">
            <view class="iTitle">
                <text>{{acquisitionActivity.activityName}}</text>
                <u-icon class="icon" name="arrow-right"></u-icon>
            </view>
            <view class="dex">{{acquisitionActivity.activityDescription||' '}}</view>
            <view class="isView">
                <view class="iv" id="sivEnd">
                    <view class="ivTit">活动结束</view>
                    <view class="ivTit1"><text class="max">{{acquisitionActivity.days}}</text><text>天</text></view>
                    <view class="ivTime">
                        <view class="ivtTag">{{acquisitionActivity.hours|timeFilter}}</view>
                        :<view class="ivtTag">{{acquisitionActivity.minutes|timeFilter}}</view>
                        :<view class="ivtTag">{{acquisitionActivity.seconds|timeFilter}}</view>
                    </view>
                </view>
                <view class="iv">
                    <view class="ivTit" style="opacity: 0;">1</view>
                    <view class="ivTit1"><text class="max">{{acquisitionActivity.peopleCount}}</text></view>
                    <view class="ivTime">
                        <text>报名人数</text>
                    </view>
                </view>
                <view class="iv">
                    <view class="ivTit" style="opacity: 0;">1</view>
                    <view class="ivTit1"><text class="max">{{acquisitionActivity.popularity}}</text></view>
                    <view class="ivTime">
                        <text>人气值</text>
                    </view>
                </view>
                <view class="iv">
                    <view class="ivTit" style="opacity: 0;">1</view>
                    <view class="ivTit1"><text class="max">{{acquisitionActivity.activityVisits}}</text></view>
                    <view class="ivTime">
                        <text>访问量</text>
                    </view>
                </view>
            </view>
            <view class="isBtn"><text>我要报名</text></view>
        </view>
        
        <!-- tab -->
        <view class="tabView">
            <tTabs></tTabs>
            <u-icon class="icon" name="arrow-right"></u-icon>
        </view>
	
        <!-- 列表 -->
        <view class="singList">
            <view class="slView">
                <view class="slvLeft">
                    <image src="@/static/icon/0.png"></image>
                </view>
                <view class="slvCover">
                    <image class="slvcImg" src="@/static/logo.png"></image>
                    <image class="slvcPlay" src="@/static/icon/right.png"></image>
                </view>
                <view class="slvMain">
                    <view class="slvmTit">仰望星空</view>
                    <view class="slvmTxt">
                        <text>张杰</text>
                        <image src="@/static/sing/hot_0.png"></image>
                        1644.4w
                    </view>
                </view>
                <view class="slvBtn">购买</view>
            </view>
            
            <view class="slView">
                <view class="slvLeft">
                    <image src="@/static/icon/1.png"></image>
                </view>
                <view class="slvCover">
                    <image class="slvcImg" src="@/static/logo.png"></image>
                    <image class="slvcPlay" src="@/static/icon/right.png"></image>
                </view>
                <view class="slvMain">
                    <view class="slvmTit">仰望星空</view>
                    <view class="slvmTxt">
                        <text>张杰</text>
                        <image src="@/static/sing/hot_0.png"></image>
                        1644.4w
                    </view>
                </view>
                <view class="slvBtn">购买</view>
            </view>
            
            <view class="slView">
                <view class="slvLeft">
                    <image src="@/static/icon/2.png"></image>
                </view>
                <view class="slvCover">
                    <image class="slvcImg" src="@/static/logo.png"></image>
                    <image class="slvcPlay" src="@/static/icon/right.png"></image>
                </view>
                <view class="slvMain">
                    <view class="slvmTit">仰望星空</view>
                    <view class="slvmTxt">
                        <text>张杰</text>
                        <image src="@/static/sing/hot_0.png"></image>
                        1644.4w
                    </view>
                </view>
                <view class="slvBtn">购买</view>
            </view>
            
            <view class="slView">
                <view class="slvLeft">
                    <!-- <image src="@/static/icon/2.png"></image> -->
                    <view class="slvlNum">4</view>
                </view>
                <view class="slvCover">
                    <image class="slvcImg" src="@/static/logo.png"></image>
                    <image class="slvcPlay" src="@/static/icon/right.png"></image>
                </view>
                <view class="slvMain">
                    <view class="slvmTit">仰望星空</view>
                    <view class="slvmTxt">
                        <text>张杰</text>
                        <image src="@/static/sing/hot_0.png"></image>
                        1644.4w
                    </view>
                </view>
                <view class="slvBtn">购买</view>
            </view>
        </view>
        
        <!-- tabbar -->
        <u-tabbar :list="tabbar" :mid-button="true" height='58px' active-color='#FFD3AA'></u-tabbar>
    </scroll-view>
</template>

<script>
	export default {
		data() {
			return {
                swiperList:['https://cdn.uviewui.com/uview/swiper/1.jpg'],
                scrollTop:0,
                haveAcquisitionActivity:false,//是否有活动
                acquisitionActivity:{},//活动
			}
		},
        filters:{
            timeFilter(e){
                if(e.toString().length==1){
                    e = '0'+e
                }
                return e
            }
        },
        computed: {
            tabbar: function () {
                return getApp().globalData.tabbar
            }
        },
		onLoad() {
            this.getAcquisitionActivity();      //获取活动
		},
		methods: {
            scrollMain(e){
                this.scrollTop = e.detail.scrollTop
            },
            //获取活动
            getAcquisitionActivity(){
                this.$req.get('activity/acquisitionActivity').then(res=>{
                    let { data } = res
                    if(data.id){
                        let flag = true;//是否展示活动 活动过期为false
                        let nowTime = new Date()
                        
                        let ymd = data.endTime.split('T')[0].split('-')
                        let tms = data.endTime.split('T')[1].split(':')
                        let endTime = new Date(ymd[0], ymd[1]-1, ymd[2], tms[0], tms[1], tms[2])
                        
                        if(nowTime>endTime){    //如果活动结束
                            flag = false
                        }else{                  //有活动且在活动日期内
                            let time = endTime.getTime() - nowTime.getTime()
                            let days=Math.floor(time / (24*3600*1000))
                            let leave1 = time % (24*3600*1000) //计算天数后剩余的毫秒数
                            let hours = Math.floor(leave1 / (3600*1000))
                            let leave2 = leave1 % (3600*1000) //计算小时数后剩余的毫秒数
                            let minutes = Math.floor(leave2 / (60*1000))
                            let leave3 = leave2 % (60*1000) //计算分钟数后剩余的毫秒数
                            let seconds = Math.round(leave3 / 1000)
                            data.days = days
                            data.hours = hours
                            data.minutes = minutes
                            data.seconds = seconds
                        }
                        
                        this.acquisitionActivity = data
                        this.haveAcquisitionActivity = flag
                    }
                })
            },
            
            
		}
	}
</script>

